.blur-effect {

    min-width: 100vw;
    min-height: 100vh;

    /* From https://css.glass */
    /* From https://css.glass */
    background: rgba(227, 227, 227, 0.29);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(7.1px);
    -webkit-backdrop-filter: blur(7.1px);
}